* {
    font-family: 'Montserrat';
}

body {
    padding: 60px;
    background-color: #000;
    color: #dedede;
}

input {
    width: 100%;
    font-size: 1.2rem;
    color: white;
    padding: 10px;
    background-color: transparent;
    border: 1px solid white;
    border-radius: 10px;
}

input:focus {
    border: 1px solid white;
    outline: none;
}

hr {
    border: 1px solid #2e2d2d;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card {
    align-content: center;
    border-radius: 5px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card > p {
    padding: 5px;
    text-align: center;
}

.card > img {
    max-height: 150px;
}